body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.left-side {
  display: flex;
  flex-direction: column;
}

.marker-btn {
  background: none;
  border: none;
  cursor: pointer;
}

.marker-btn img {
  width: 25px;
  height: 25px;
}

.pop-description {
  white-space: pre-line;  
  vertical-align: bottom;
}

.pop-address{
  white-space: pre-line;  
  vertical-align: bottom;
}

.pop-container{
  display: flex;
  flex-direction: column;
}

.pop-header{
  font-weight: bold;
  font-size: large;
}

.searchBox-container{
  padding: 10px;
  background-color: white;
}

.controls-container > div{
  float: left;
  margin: 1rem;
  padding: 10px;
}

.navbar {
  display: flex;
  background-color: #005fa2;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 20px;
  padding-right: 20px;
  color: white;
}

.navbar a {
  margin: 1em;
  color: inherit;
  text-decoration: inherit;
}

.navbar a:hover {
  text-decoration: underline;
  font-weight: bold;
  }

  .info-icon{
    width: 25px;
    height: 25px;
  }
  .info-icon :hover{
    opacity: 1;
  }

  .checkbox{
    width: 200px;
    height: auto;
    text-align: left;
    padding-left: 10px;
    margin: 1em;
  }

  .checkbox-navbar{
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-left: 5px;
    padding-right: 5px;
    height: 25px;
  }

  .checkbox-navbar-container{
    padding: 5px;
    background-color: #005fa2;
  }

  
  .checkbox-input-container{
    background-color: rgb(221, 206, 138);
    padding: 15px;
    text-align: left;
  }

  .check img{
    width: 20px;
    height: 20px;
    padding-left: 10px;
  }
  .check{
    display: flex;
    flex-direction: row;
    padding:5px;
  }

  .check-submit{
    padding: 10px;
  }

  .info-window{
    width: 400px;
    height: auto;
    position: absolute;
    bottom: 10%;
    right: 0px;
    margin: 1em;
}

.info-window-container{
  background-color: rgb(221, 206, 138);
  padding: 15px;
  text-align: left;
}

.info-window-close-bttn :hover{
  opacity: 1;
  color: red;
}


.info-navbar{
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 5px;
  padding-right: 5px;
  height: 25px;
}

.info-navbar-container{
  padding: 5px;
  background-color: #005fa2;
}

.info-input-container{
  background-color: rgb(221, 206, 138);
  padding: 15px;
  text-align: left;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

